import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const NotFoundPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title
  const webArchiveUrl = "https://web.archive.org/web/*/" + location.host + location.pathname
  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="404: сторінку не знайдено"/>
      <h1>404: сторінку не знайдено</h1>
      <p>Якщо ви впевнені, що раніше тут щось було - подивіться у <a
        href={webArchiveUrl}>Web-архіві ({webArchiveUrl})</a>.</p>
    </Layout>
  )
}

export default NotFoundPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
